'use strict';

layui.define(['element', 'layer', 'tabController', 'renderNav'], function (exports) {
    var layer = layui.layer;
    var element = layui.element;
    var renderNav = layui.renderNav;
    var tabController = layui.tabController;
    tabController.init();
    //渲染左侧菜单
    $.get('/Admin/Index/getMenu', function (data) {
        var $nav = $(".navBar");
        if ($nav.html() === '') {
            $nav.html(renderNav(data)).height($(window).height() - 245);
            element.render('nav');
            // 左侧点击事件
            $nav.on("click", ".layui-nav .layui-nav-item a", function () {
                //如果不存在子级
                if ($(this).siblings().length === 0) {
                    var _this = $(this);
                    var title = _this.find("cite").text();
                    var url = _this.attr("data-url");
                    var option = {
                        title: title,
                        url: url
                    };
                    tabController.go(option);
                    $('body').removeClass('site-mobile'); //移动端点击菜单关闭菜单层
                }
                // $(this).parent("li").siblings().removeClass("layui-nav-itemed");
            });
            $(window).resize(function () {
                $nav.height($(window).height() - 245);
            });
        }
    });
    //隐藏左侧导航
    $(".hideMenu").click(function () {
        $(".layui-layout-admin").toggleClass("showMenu");
    });
    //刷新当前
    $(".refresh").on("click", function () {
        tabController.refresh();
    });
    //关闭其他
    $(".closePageOther").on("click", function () {
        var $ul = $("#top_tabs");
        var $li = $ul.children('li');
        var active_tab_id = $ul.children('li.layui-this').attr('lay-id');
        var ids = [];
        $li.each(function () {
            var id = $(this).attr("lay-id");
            if (id && id !== active_tab_id) {
                ids.push(id);
            }
        });
        tabController.deleteTabs(ids);
    });
    //关闭全部
    $(".closePageAll").on("click", function () {
        var $li = $("#top_tabs").children('li');
        if ($li.length > 1) {
            var ids = [];
            $li.each(function () {
                var id = $(this).attr("lay-id");
                if (id) {
                    ids.push(id);
                }
            });
            tabController.deleteTabs(ids);
        } else {
            layer.msg("没有可以关闭的窗口了@_@");
        }
    });
    exports('indexEntry');
});